
/**
 * @imports
 */
import { Console } from './index.js';
import Subscript from '../Subscript.js';

/**
 * @Console2
 */
export default class Console2 extends Console {

    connectedCallback() {
        super.connectedCallback();
        Prism.plugins.toolbar.registerButton( 'run', {
            text: 'Run',
            onClick: () => {
                this.setEditable( false );
            }
        });
    }

    setEditable( editable ) {
        super.setEditable( editable );
        if ( editable ) {
            if ( this.nowRunning ) {
                this.nowRunning.dispose();
                this.nowRunning = null;
            }
        } else {
            if ( !this.nowRunning ) {
                this.nowRunning = Subscript( this._codeBlock.textContent );
                this.bind( this.nowRunning, false );
            }
            this.nowRunning();
        }
    }

}

customElements.define( 'subscript-console2', Console2 );
